import { useHistory, useLocation } from 'react-router';
import classes from './NavButton.module.scss';

const NavButton = ({ route, buttonText, navType, closeMobileMenu }) => {
    const history = useHistory();
    const location = useLocation();

    const activeClassChecker = () => {
        const routes = location.pathname.split('/');
        routes.shift();
        // console.log(`routes: ${routes[1]}`);
        // console.log(`route: ${route}`);
        // console.log(`pathname: ${location.pathname}`);
        // console.log(routes.length);
        if (routes.length > 1 && routes[1] !== route) {
            //console.log('homepage match');
            return null;
        }

        //console.log(routes[1]);

        if (routes.length === 1 && routes[0] !== route) {
            // console.log('1st null');
            return null;
        }

        if (routes.length !== 1 && routes[1] !== route) {
            // console.log('2nd null');
            return null;
        }

        // const matched = matchPath(location.pathname, route);
        // console.log(matched);
        // if (matched == null) {
        //     return null;
        // }
        return classes.active;
    };
    const navClickHandler = route => {
        if (navType === 'mobile') {
            closeMobileMenu();
        }
        if (route === 'portfolio') {
            history.push('/portfolio');
        } else {
            history.push('/portfolio/' + route);
        }
        //history.push();
    };
    return (
        // <NavLink
        //     to={route}
        //     className={classes['nav-button']}
        //     activeClassName={classes.active}
        //     onClick={navClickHandler}
        // >
        //     {buttonText}
        //     {/* <div className={classes['active-underline']}></div> */}
        // </NavLink>

        <button
            className={`${classes['nav-button']} ${activeClassChecker()}`}
            onClick={() => {
                navClickHandler(route);
            }}
        >
            {buttonText}
            <div className={classes['active-underline']}></div>
        </button>
    );
};

export default NavButton;
